@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
:root{
  --primary-clr :rgb(95, 22, 129);
  --secondary-clr:#F1F4FFFF ;
  --secondary-clr-1:#F1F4FFFF;
}
*{
  font-family:"nunito",sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;

}
body {
  background: #F1F4FFFF;
  background: var(--secondary-clr-1);
  font-family: "nunito";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  font-family: 'Quicksand', sans-serif;
  font-weight: 300;;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
 }

 body::-webkit-scrollbar {
  width: 1em;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(95, 22, 129);
  background-color: var(--primary-clr);
  outline: 1px solid slategrey;
  border-radius: 5px;
}

.home{
  width: 100%;
  height: 100vh;
  background:linear-gradient(#000000bd,rgba(0, 0, 0, 0.863)), url("https://images.unsplash.com/photo-1495954222046-2c427ecb546d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1489&q=80") center/cover no-repeat;
  color: var(--secondary-clr);
  display: flex;
  flex-direction: column;
  align-items: center;

  

}

.home-text{
  margin-top: 8rem;
margin-left: 2rem;

}

.home-text p{
  font-size: 3.5rem;
  font-weight: bolder;
  
}
@media(min-width:689px){
  .home-text{
    text-align: center;
  }
}

.home-text b{
  color:var(--primary-clr) ;
  font-size: 5rem;
}

.scroll-container{
  text-align: center;
  margin-top: 2rem;
  cursor: pointer;
  color: var(--secondary-clr);
}


.scroll-icons{
  /* border: 2px solid black; */
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: 1.5rem;
  cursor: pointer;
  -webkit-animation: scroll 0.5s ease-in-out 0.5s infinite ;
          animation: scroll 0.5s ease-in-out 0.5s infinite ;
}

@-webkit-keyframes scroll{
 0%{
   -webkit-transform: translateY(-10%);
           transform: translateY(-10%);
 }
 50%{
   -webkit-transform: translateY(0);
           transform: translateY(0);
 }
 100%{
   -webkit-transform: translateY(10%);
           transform: translateY(10%);
 }
}

@keyframes scroll{
 0%{
   -webkit-transform: translateY(-10%);
           transform: translateY(-10%);
 }
 50%{
   -webkit-transform: translateY(0);
           transform: translateY(0);
 }
 100%{
   -webkit-transform: translateY(10%);
           transform: translateY(10%);
 }
}
header{
  min-width: 100%;
  /* position: fixed; */
  background: var(--primary-clr) ;
  height: 60px;
  display: flex;
  align-items: center;
  color: var(--secondary-clr-1) ;
  border-bottom: 5px solid var(--secondary-clr-1);
  /* z-index: 5; */
}

nav{
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
}

nav h1{
  margin-left: 1rem;
  font-size: 2rem;
  font-weight: bolder;
  color: var(--secondary-clr-1);
}

span{
  display: inline-block;
}
.nav-icon{
  margin: auto 1rem auto auto;
  font-size: 2rem;
  cursor: pointer;
}

/* .nav-icon:hover{
  color: var(--secondary-clr);
} */

.nav-links{
  list-style: none;
  display: block;
  position: absolute;
  top: 48px;
  text-align: center;
  width: 100%;
  background: var(--primary-clr) ;
  height: auto;
  padding: 0 ;
  z-index: 99;
  border-bottom: 3px solid var(--secondary-clr-1);
 font-weight: bolder;
 padding-bottom:6px ;

}
.nav-links li{
  font-size: 1.5rem;
  padding: 1rem 2rem;
  cursor: pointer;
}

.nav-links li:hover{
 -webkit-filter: brightness(0.5);
         filter: brightness(0.5);
}

.toggle{
  display: none;
}


@media (min-width:689px){
  nav{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 90%;
    margin: 0 auto;
  }

  nav h1{
    margin: 0;
  }
  .nav-links{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
    position: relative;
    top: 0;
    padding: 0 ;
    border: none;
    max-width: 500px;
  }

  .nav-links li{
    font-size: 1.4rem;
    padding: 0;
    cursor: pointer;
  }
  .nav-icon{
    display: none;
  }
}




.about{
  max-width: 100%;
  padding: 30px;
  display: grid;
  margin: auto;
  text-align: center;
  color: var(--primary-clr);
}







.about > h1{
  font-size: 2.5rem;
  font-weight: bolder;
  padding: 0;
  margin-bottom: 0.6rem;
}

.about > h1 b{
  color: var(--primary-clr);
}

.about-underline{
  background: var(--primary-clr);
  width: 80px;
  margin: auto;
  height: 4px;
  margin-top: 0;

}

.about-content{
  width: 100%;
  margin: 2rem auto;
  display: grid;
}

.about-image{
  width: 60%;
  margin: auto;
  display: block;
  border-radius: 50%;
  border: 5px solid var(--primary-clr);
  object-fit: cover;
  
}

.about-text{
  margin-top: 2rem;
  color: var(--primary-clr);
  font-weight: 900;
  letter-spacing: 1px;
  line-height: 1.6;
}

@media(min-width:960px){
  .about-content{
    width: 80%;
    margin: auto;
    margin-top: 3rem;
    max-height: 400px;
    grid-template-columns: repeat(auto-fill,minmax(350px,1fr));
    
  }

  .about-image{
    /* margin-top: 1.5rem; */
    width: 250px;
    border-radius: 5px;
    border: 2px solid var(--primary-clr);
    height: 100%;
    max-height: 500px;
  }

  .about-text{
    text-align: left;
    padding: 0;
    /* max-width: 500px; */
  }
}

.carousel-container{
  width: 100%;
  margin: 4rem auto;
 
}

.skills-icon{
  margin-bottom: 2.5rem;
}

.skills-icon h1{
  color: var(--primary-clr);
}

.skills-icon span{
  display: inline-block;
  font-size: 5rem;
  color: var(--primary-clr);
  cursor: pointer;
}
.project-container{
  width: 100%;
  /* height: auto; */
  max-width: 100%;
  font-family: "nunito";
}


.project-container h1{
  text-align: center;
  color: var(--primary-clr);
}

.project-underline{
  background: var(--primary-clr);
  width: 80px;
  margin: auto;
  height: 4px;
  margin-top: 0;
}

.project-list-container{
  width: 90%;
  margin: 4rem auto;
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  grid-template-columns: repeat(auto-fill,minmax(350px,1fr));
}

.single-project{
  position: relative;
  width: 100%;
  height: 300px;
  border-radius: 5px;
  border: 2px solid var(--primary-clr);
  cursor: pointer;
  box-shadow: inset 2px inset 2px 5px var(-primary-clr);
  overflow: hidden;

}

.single-project img{
  width: 100%;
  height: 100%;
  display: inline-block;
  object-fit: fill;
}

.project-detail{
  width: 100%;
  height: 50%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  -webkit-backdrop-filter: blur(35px);
          backdrop-filter: blur(35px);
  background-clip: padding-box;
  display: grid;
  place-items: center;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  overflow: hidden;
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
}


.single-project:hover .project-detail{
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.project-detail-btn{
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  color:var(--secondary-clr-1);
  font-size: 1.2rem;
  text-align: center;
  padding: 0.5rem 2rem;
  border: 2px solid var(--primary-clr);
  border-radius: 5px;
  background: var(--primary-clr);
}

.project-detail-btn:hover{
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}
.contact-container{
  width: 100%;
  margin: 10rem auto;
  color: var(--primary-clr);
  text-align: center;

}

.contact-underline{
  width: 100px;
  height: 4px;
  margin: 1rem auto;
  border-radius: 5px;
  background: var(--primary-clr);
}

.contact-item-container{
  width: 80%;
  margin: 3rem auto;
  display: grid;
  grid-gap: 3rem;
  gap: 3rem;
  grid-template-columns: repeat(auto-fill,minmax(350px,1fr));
}

.form-group{
  width: 100%;
  margin: 1rem auto;
}


input[type=email] {
  padding: 10px 20px;
  border: 2px solid var(--primary-clr);
  border-radius: 5px;
  background: var(--secondary-clr-1);
  width: 100%;
  outline: none;
}

input[type=email]:focus {
  background: transparent;
  border: 3px solid grey;
  color: var(--primary-clr);
}




textarea{
  padding: 10px 20px;
  outline: none;
  width: 100%;
  height: 350px;
  resize: none;
  overflow: auto;
  border: 2px solid var(--primary-clr);
  border-radius: 5px;
  background: var(--secondary-clr-1);
  color: var(--primary-clr);
}

textarea:focus{
  border: 3px solid grey;
}

.form-btn{
  outline: none;
  padding: 10px 40px;
  border: none;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  cursor: pointer;
  border-radius: 5px;
  background: var(--primary-clr);
  color: var(--secondary-clr-1);
}

.form-btn:hover{
  -webkit-filter: brightness(1.3);
          filter: brightness(1.3);
}

.contact-detail-container{
  width: 100%;
  margin: auto;
  /* border: 2px solid green; */
}

.contact-detail{
  display: flex;
  align-items: center;
  /* justify-content: space-evenly; */
  max-width: 260px;
  margin-bottom: 2rem;
}

.contact-detail span{
  display: inline-block;
  font-size: 3.5rem;
  margin-right: 1rem;
  color: var(--primary-clr);
}
.scroll-to-top span{
  color: var(--secondary-clr);
  position: fixed;
  bottom: 70px;
  right: 20px;
  z-index: 7;
  font-size: 1.5rem;
  background: var(--primary-clr);
  width: 50px;
  height: 50px;
  display: grid;
  place-items: center;
  border-radius: 50%;
  cursor: pointer;
  -webkit-animation: scroll 2s ease-in-out infinite;
          animation: scroll 2s ease-in-out infinite;
}


@-webkit-keyframes scroll{
  from{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}


@keyframes scroll{
  from{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}
.footer-container{
  width: 100%;
  height: 100%;
  background: var(--primary-clr);
  color: var(--secondary-clr);
  text-align: center;
}

.social-media-container{
  width: 90%;
  margin: 0 auto;
}

.social-media-container a{
display: inline-block;
margin-right: 1rem;
margin-top: 1rem;
}

p{
  margin-top: 1rem;
  padding-bottom: 2rem;
}

